export default {
	props: [],
	data() {
		
		return {
			id:'',
			quantity:'',
			nearby: ['', '', '', '', '', '', '', ''],
			hot: ['', '', '', '', '', '', '', ''],
		}
	},
	components: {

	},
	methods: {
		back(){
			this.$router.go(-1);
		},
		search(){
			this.$router.push({ name: 'allStoreGoods', params: {id:this.id,keyword:this.quantity} });
		}
	},
	mounted() {
		this.id = this.$route.params.id;
		console.log("id",this.id);
	}
}



